@import url("https://fonts.googleapis.com/css2?family=ABeeZee&display=swap");

.App {
  text-align: center;
  font-family: "ABeeZee", serif;
}

.App-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  background-color: #282c34;
  min-height: 85vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-Choice-question {
  font-size: calc(30px + 10vmin);
}

.App-Question-Button {
  background: transparent;
  border: 1vh solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  font-size: 4em;
  font-weight: 700;
  line-height: 1;

  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: none;
  position: relative;
  top: 0;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
  color: #fff;
}

.App-Question-Button:hover {
  font-size: 4.2em;
}

.App-Choice-button {
  border: 1vh solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  font-size: calc(10px + 10vmin);
  font-weight: 700;
  line-height: 1;
  margin: 18px;
  padding: 2vh 5vh;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: none;
  position: relative;
  top: 0;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
  background: #3cbce1;
  color: #fff;
  box-shadow: 0 3px 0 #1fa4cb;
}

.App-Choice-button:hover {
  background: #56d7ed;
}
.App-Choice-button:active {
  background: #2fb7df;
  transform: scale(1.1);
  transition: 0.1s;
}
.App-Choice-button:focus {
  background: #2fb7df;
}

.App-Choice-button:disabled {
  background: #51686e;
}

.App-GreenGem-Img {
  width: 10vh;
  background: transparent;
}

.App-NewGem-Img {
  width: 10vh;
  background: transparent;
  filter: hue-rotate(300deg);
}
.App-Gem-Img {
  width: 10vh;
  background: transparent;
}

.App-Gem-Button {
  width: 12vh;
  background: transparent;
  border: solid transparent;
}
.App-Gem-Button:hover,
.App-GemInventory-Button:hover {
  transform: scale(1.1);
  transition: 0.1s;
  cursor: pointer;
}

.App-Gem-Button:focus,
.App-GemInventory-Button:focuse {
  outline: 0;
}

.App-GemInventory-Button {
  width: 12vh;
  background: transparent;
  border: solid transparent;
}

.heart {
  width: 8vh;
}
.App-Heart {
  background: transparent;
  border: solid transparent;
}
.App-Heart:focus {
  outline: 0;
}
.App-Heart:hover {
  transform: scale(1.1);
  transition: 0.1s;
  cursor: pointer;
}

.score {
  font-size: calc(30px + 4vmin);
}

.App-Nav-button {
  background: transparent;
  border: 1vh solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  font-size: calc(4px + 3vmin);
  font-weight: 700;
  line-height: 1;
  margin: 0;
  padding: 1vh 3vh;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: none;
  position: relative;
  top: 0;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  background: transparent;
  color: #fff;
}

.App-Nav-button:hover {
  background: #717c8f;
}
.App-Nav-button:active,
.App-Nav-button:focus {
  background: #717c8f;
  transform: scale(1.1);
  transition: 0.1s;
}

.App-Menu-Button {
  border: none; /* Remove borders */
  background: transparent;
  box-sizing: border-box;
  cursor: pointer;
  padding: 0.5vh 0.5vh;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: none;
  position: relative;
  top: 0;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
  color: #fff;
}

.App-Menu-Image {
  width: 8vh;
  filter: invert(100%);
}

.App-Menu-Image:hover {
  width: 8vh;
  filter: invert(50%);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
  transform: scale(1.1);
  transition: 0.1s;
}

.App-Retry-Button {
  border: none; /* Remove borders */
  background: transparent;
  box-sizing: border-box;
  cursor: pointer;
  padding: 0.5vh 0.5vh;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: none;
  position: relative;
  top: 0;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.App-Retry {
  width: 20vh;
  filter: invert(100%);
}

/* Coin Bag Zone */

.App-CoinBag-Button,
.App-Coin-Button {
  background: transparent;
  border: 1vh solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  color: aliceblue;
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.CoinBag {
  width: calc(4px + 6vmin);
}

.App-CoinBag-Button:hover,
.App-Coin-Button:hover,
.App-Retry-Button:hover {
  transform: scale(1.1);
  transition: 0.1s;
  cursor: pointer;
}

.Coin {
  width: calc(4px + 6vmin);
}

.App-Coins-Div:hover,
.App-Coins-Div:active {
  padding: none;
}

.App-Coins-Div {
  padding: none;
}

.App-NoCoin-Button {
  background: transparent;
  border: 0vh solid transparent;
  padding: 0vh;
  box-sizing: border-box;
  cursor: pointer;
  font-size: 1em;
  color: aliceblue;
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.App-NoCoin-Button:hover {
  font-size: 1.1em;
}

.Greeting {
  font-size: calc(5px + 7vmin);
}

.App-Nav-Img {
  width: calc(5px + 7vmin);
  filter: invert(100%);
}

/* Item Zone */

.App-Item-Locked {
  background: transparent;
  max-width: 100%;
  height: auto;
  filter: blur(20px);
}

.App-Item-Unlocked {
  background: transparent;
  max-width: 100%;
  height: auto;
  filter: blur(0px);
}

.App-Item-Currency {
  background: transparent;
  width: calc(4px + 6vmin);
}

/* Shop */

.App-Shop-Item {
  background: transparent;
  padding: 50px;
}

.App-JustHover-Button {
  background: transparent;
  border: solid transparent;
  font-size: 1em;
  color: aliceblue;
  cursor: pointer;
  display: inline-block;
  outline: none;
  position: relative;
}

.App-JustHover-Button:hover {
  transform: scale(1.05);
  transition: 0.1s;
  cursor: pointer;
}

.App-JustHover-Button:focus {
  outline: 0;
}

.App-Welcome-Price{
  background: transparent;
  max-width: 100px;
  height: auto;
  filter: blur(0px);
}